import {
  Container,
  Grid,
  ImageList,
  ImageListItem,
  Paper,
  Typography,
} from '@mui/material'
import { Box } from '@mui/system'
import Design1 from '../assets/images/design-1.png'
import Design2 from '../assets/images/design-2.png'
import Design3 from '../assets/images/design-3.png'
import Design4 from '../assets/images/design-4.png'
import Design5 from '../assets/images/design-5.png'
import Design6 from '../assets/images/design-6.png'
import Design7 from '../assets/images/design-7.png'

const images = [
  { image: Design1, row: 1, col: 1 },
  { image: Design2, row: 1, col: 2 },
  { image: Design3, row: 1, col: 2 },
  { image: Design4, row: 2, col: 1 },
  { image: Design5, row: 2, col: 1 },
  { image: Design6, row: 1, col: 1 },
  { image: Design7, row: 1, col: 2 },
]

const Designs = () => {
  return (
    <Container maxWidth="md" sx={{ pt: 5, position: 'relative', top: '-50px' }}>
      <Paper sx={{ p: 5 }}>
        <Grid container>
          <Grid item xs={12}>
            <ImageList
              sx={{ width: '100%' }}
              rowHeight={400}
              variant="quilted"
              title="Diseños"
              cols={3}
              gap={8}
            >
              {images.map((item) => (
                <ImageListItem
                  cols={item.col || 1}
                  rows={item.row || 1}
                  sx={{ cursor: 'pointer' }}
                >
                  <img src={item.image} alt="diseños" loading="lazy" />
                </ImageListItem>
              ))}
            </ImageList>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default Designs
